import React from 'react';
import { IconArrowUpRight } from '../common/Icons';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { media, rem } from '../../lib/styles/variables';
import palette from '../../lib/styles/palette';

const FnbBlock = styled.ul`
  display: flex;
  flex-wrap: wrap;
  row-gap: 30px;
  width: 688px;

  > li {
    width: 25%;
    span {
      display: inline-block;
      font-weight: 700;
      margin-bottom: 20px;
    }
  }
  ul {
    font-size: ${rem(14)};
    display: flex;
    flex-direction: column;
    gap: 15px;
    li {
      width: 100%;
    }
    a {
      display: inline-block;
      width: 100%;
      &:hover {
        color: ${palette.gray[7]};
      }
    }
  }

  ${media('medium')} {
    flex-wrap: nowrap;
    flex-direction: column;
    width: auto;
    > li {
      width: auto;
    }
  }
`;

const Fnb = () => {
  return (
    <FnbBlock>
      <li>
        <span></span>
        <ul>
          <li>
            <Link to="/"> </Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"> </Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
        </ul>
      </li>
      <li>
        <span></span>
        <ul>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
        </ul>
      </li>
      <li>
        <span></span>
        <ul>
          <li>
            <Link to="/"> </Link>
          </li>
          <li>
            <Link to="/"> </Link>
          </li>
          <li>
            <Link to="/"> </Link>
          </li>
        </ul>
      </li>
      <li>
        <span>Karrot</span>
        <ul>
          <li>
            <Link to="/">
              Canada <IconArrowUpRight />
            </Link>
          </li>
          <li>
            <Link to="/">
              United States <IconArrowUpRight />
            </Link>
          </li>
          <li>
            <Link to="/">
              United Kingdom <IconArrowUpRight />
            </Link>
          </li>
          <li>
            <Link to="/">
               <IconArrowUpRight />
            </Link>
          </li>
        </ul>
      </li>
      <li>
        <span></span>
        <ul>
          <li>
            <Link to="/">IR</Link>
          </li>
          <li>
            <Link to="/">PR</Link>
          </li>
          <li>
            <Link to="/"></Link>
          </li>
        </ul>
      </li>
    </FnbBlock>
  );
};

export default Fnb;
