const BuySellFilter = () => {
  return (
    <FilterTemplate>
      <BuySellFilterContent />
    </FilterTemplate>
  );
};

const FilterInputWrapper = styled.div`
  font-size: ${rem(14)};
  > div {
    display: flex;
    margin: ${rem(15)} 0 ${rem(10)};
    align-items: center;
    input {
      flex: 1;
      border: 1px solid ${palette.gray[4]};
      border-radius: 5px;
      padding: 0.3em 0.5em;
      &::placeholder {
        color: ${palette.gray[8]};
      }
      &:focus {
        outline: 2px solid ${palette.gray[9]};
      }
    }
    span {
      margin: 0 5px;
      font-size: 1.2em;
    }
  }
  > button {
    text-decoration: underline;
    color: ${palette.gray[8]};
  }
`;

const BuySellFilterContentBlock = styled.div`
  .location {
    > span {
      &::before {
        display: none;
      }
    }
    > button {
      padding-left: ${rem(10)};
    }
    > div {
      padding-left: ${rem(20)};
    }
  }
  .price {
    .price-buttons {
      display: flex;
      flex-direction: column;
    }
  }

  ${({ modal }) =>
    modal &&
    css`
      .location > div,
      .category,
      .sort {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
      }
      .price {
        .price-buttons {
          display: block;
          white-space: nowrap;
          overflow: auto;
        }
      }
      ${media('small')} {
        .location > div,
        .category,
        .sort {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
        }
      }
    `}
`;

export const BuySellFilterContent = (props) => {
  return (
    <BuySellFilterContentBlock {...props}>
      <FilterListTemplate>
        <FilterItemCheckbox className="checked">
            
        </FilterItemCheckbox>
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <div className="location">
          <FilterItemRadio as="span"></FilterItemRadio>
          <FilterItemRadio className="checked"></FilterItemRadio>
          <div>
            <FilterItemRadio></FilterItemRadio>
            <FilterItemRadio></FilterItemRadio>
            <FilterItemRadio></FilterItemRadio>
            <FilterItemRadio></FilterItemRadio>
            <FilterItemRadio></FilterItemRadio>
          </div>
        </div>
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <div className="category">
          {[
            '',
            '',
            '/',
            '/',
            '',
            '',
            '',
            '',
            '/',
            '/',
            '/',
            '//',
            '',
            '/',
            '',
            '',
            '',
            '',
            ' ',
            '',
          ].map((item) => (
            <FilterItemRadio
              key={item}
              className={item === '' && 'checked'}
            >
              {item}
            </FilterItemRadio>
          ))}
        </div>
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <div className="sort">
          <FilterItemRadio className="checked"></FilterItemRadio>
          <FilterItemRadio></FilterItemRadio>
        </div>
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <div className="price">
          <div className="price-buttons">
            <FilterItemButton className="checked"></FilterItemButton>
            <FilterItemButton>5,000 </FilterItemButton>
            <FilterItemButton>10,000 </FilterItemButton>
            <FilterItemButton>20,000 </FilterItemButton>
          </div>
          <FilterInputWrapper>
            <div>
              <input type="number" value="0" />
              <span>-</span>
              <input type="number" value="0" />
            </div>
            <button></button>
          </FilterInputWrapper>
        </div>
      </FilterListTemplate>
    </BuySellFilterContentBlock>
  );
};

export default BuySellFilter;
