import React from 'react';
import styled from 'styled-components';
import { verticalLine } from '../../lib/styles/variables';

const CorpInfo = () => {
  return (
    <>
      <Info />
      <Policy />
    </>
  );
};

const InfoBlock = styled.div`
  line-height: 1.5;
  dt,
  dd {
    display: inline-block;
  }
  dt {
    margin-right: 5px;
  }
  b,
  dt {
    font-weight: 700;
  }
  .inline {
    display: inline;
    &:nth-child(1),
    &:nth-child(7) {
      ${verticalLine({ height: '50%' })}
    }
  }
`;

const Info = () => {
  return (
    <InfoBlock>
      <b>() </b>
      <dl>
        <div className="inline">
          <dt></dt>
          <dd>, </dd>
        </div>
        <div className="inline">
          <dt></dt>
          <dd>375-87-00088</dd>
        </div>
        <div>
          <dt> </dt>
          <dd>J1200020200016</dd>
        </div>
        <div>
          <dt> </dt>
          <dd>2021--2875</dd>
        </div>
        <div>
          <dt> </dt>
          <dd>Amazon Web Service(AWS)</dd>
        </div>
        <div>
          <dt></dt>
          <dd>   300, 10 ()</dd>
        </div>
        <div className="inline">
          <dt></dt>
          <dd>1544-9796</dd>
        </div>
        <div className="inline">
          <dt></dt>
          <dd>cs@daangnservice.com</dd>
        </div>
      </dl>
    </InfoBlock>
  );
};

const PolicyBlock = styled.ul`
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  a {
    font-weight: 600;
  }
`;

const Policy = () => {
  return (
    <PolicyBlock>
      <li>
        <a href="/"></a>
      </li>
      <li>
        <a href="/"></a>
      </li>
      <li>
        <a href="/"></a>
      </li>
      <li>
        <a href="/"> </a>
      </li>
      <li>
        <a href="/">  </a>
      </li>
      <li>
        <a href="/"></a>
      </li>
    </PolicyBlock>
  );
};

export default CorpInfo;
