import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { media, rem } from '../../lib/styles/variables';
import palette from '../../lib/styles/palette';
import { GoodsReactionStats } from '../common/market/GoodsArticleLayout';

const BuySellGoodsDescBlock = styled.div``;

const GoodsName = styled.h1`
  font-size: ${rem(26)};
  font-weight: 700;
  margin-bottom: 0.8em;
  ${media('large')} {
    font-size: ${rem(24)};
  }
  ${media('small')} {
    font-size: ${rem(20)};
  }
`;

const GoodsCategory = styled.div`
  color: ${palette.gray[7]};
  margin-bottom: ${rem(30)};
  a {
    text-decoration: underline;
  }
  ${media('large')} {
    font-size: ${rem(15)};
  }
  ${media('medium')} {
    margin-bottom: ${rem(16)};
  }
  ${media('small')} {
    font-size: ${rem(13)};
  }
`;

const GoodsPrice = styled.div`
  font-weight: 700;
  font-size: ${rem(24)};
  ${media('large')} {
    font-size: ${rem(20)};
  }
  ${media('small')} {
    font-size: ${rem(18)};
  }
`;

const GoodsIntro = styled.p`
  line-height: 1.5;
  margin: 1.5em 0;
  font-size: ${rem(18)};
  white-space: pre-line;
  ${media('large')} {
    font-size: ${rem(16)};
  }
  ${media('medium')} {
    font-size: ${rem(18)};
  }
  ${media('small')} {
    font-size: ${rem(16)};
  }
`;

const BuySellGoodsDesc = () => {
  return (
    <BuySellGoodsDescBlock>
      <GoodsName>VOV   </GoodsName>
      <GoodsCategory>
        <Link></Link>   6 
      </GoodsCategory>
      <GoodsPrice>33,000</GoodsPrice>
      <GoodsIntro>
        {`   .
   55-55       . 

      !  3   .

3  ~    ,   / .`}
      </GoodsIntro>
      <GoodsReactionStats chat={1} like={5} look={23} />
    </BuySellGoodsDescBlock>
  );
};

export default BuySellGoodsDesc;
