import React from 'react';
import FilterTemplate, {
  FilterListTemplate,
} from '../common/market/FilterTemplate';
import {
  FilterItemButton,
  FilterItemCheckbox,
  FilterItemRadio,
  FilterItemSlide,
} from '../common/market/FilterItem';
import styled from 'styled-components';
import { rem } from '../../lib/styles/variables';

const CarsFilter = () => {
  return (
    <FilterTemplate>
      <CarsFilterContent />
    </FilterTemplate>
  );
};

const CarsFilterBrand = styled.div`
  max-height: ${rem(277)};
  overflow: auto;
  img {
    transform: translateY(${rem(4)});
    width: ${rem(32)};
    margin-right: ${rem(5)};
  }
`;

export const CarsFilterContent = () => {
  return (
    <div>
      <FilterListTemplate heading="">
        <CarsFilterBrand>
          {[
            '',
            '',
            '',
            '()',
            '',
            'KGM',
            '',
            'BMW',
            '',
            '',
            '',
          ].map((item, i) => (
            <FilterItemRadio key={item} className={i === 0 && 'checked'}>
              {i > 0 && <img src={`${process.env.PUBLIC_URL}/images/cars_brand_img_${i}.png`} alt="" />}
              {item}
            </FilterItemRadio>
          ))}
        </CarsFilterBrand>
      </FilterListTemplate>
      <FilterListTemplate heading="">
        {[
          '/',
          '/',
          '/',
          'SUV/RV',
          '/',
          '',
        ].map((item, i) => (
          <FilterItemCheckbox key={item} className={i === 0 && 'checked'}>
            {item}
          </FilterItemCheckbox>
        ))}
      </FilterListTemplate>
      <FilterListTemplate heading="">
        {[
          ' ()',
          ' (+)',
          ' ()',
          ' (+)',
          'LPG',
          ' (LPG+)',
          ' (LPG+)',
          'CNG ()',
          '',
          '',
          '',
          '',
        ].map((item, i) => (
          <FilterItemCheckbox key={item}>{item}</FilterItemCheckbox>
        ))}
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <FilterItemSlide
          points={[
            '100',
            '200',
            '300',
            '500',
            '700',
            '1000',
            '3000',
            '5000',
            '7000',
            '1',
          ]}
        />
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <FilterItemSlide min={1994} max={2025} unit="" />
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <FilterItemSlide min={2} max={20} step={2} unit="km" />
      </FilterListTemplate>
      <FilterListTemplate heading="">
        {[' (A/T)', ' (M/T)'].map((item, i) => (
          <FilterItemButton key={item} className={i === 0 && 'checked'}>
            {item}
          </FilterItemButton>
        ))}
      </FilterListTemplate>
      <FilterListTemplate heading=" ">
        {['', ' ', ''].map((item) => (
          <FilterItemButton>{item}</FilterItemButton>
        ))}
      </FilterListTemplate>
    </div>
  );
};

export default CarsFilter;
