import React from 'react';
import FilterTemplate, {
  FilterListTemplate,
} from '../common/market/FilterTemplate';
import {
  FilterItemCheckbox,
  FilterItemSlide,
} from '../common/market/FilterItem';

const RealtyFilter = () => {
  return (
    <FilterTemplate>
      <RealtyFilterContent />
    </FilterTemplate>
  );
};

export const RealtyFilterContent = () => {
  return (
    <div>
      <FilterListTemplate heading=" ">
        {[
          '',
          '',
          '',
          '',
          '',
          '(, ,  )',
        ].map((item, i) => (
          <FilterItemCheckbox key={item} className={i === 0 && 'checked'}>
            {item}
          </FilterItemCheckbox>
        ))}
      </FilterListTemplate>
      <FilterListTemplate heading=" /">
        {['', '', '', ''].map((item, i) => (
          <FilterItemCheckbox key={item} className={i === 0 && 'checked'}>
            {item}
          </FilterItemCheckbox>
        ))}
      </FilterListTemplate>
      <FilterListTemplate heading="">
        <FilterItemSlide
          subTitle=""
          points={[2, 4, 6, 8, 10, 15, 20, 25, 30, 40, 50, 60, 70]}
          unit=""
        />
      </FilterListTemplate>
      <FilterListTemplate heading="">
        {['', '1', '2~5', '6~9', '10 '].map((item) => (
          <FilterItemCheckbox key={item}>{item}</FilterItemCheckbox>
        ))}
      </FilterListTemplate>
      <FilterListTemplate heading=" ">
        {[
          ' ',
          ' ',
          ' ',
          '',
          '',
          '',
        ].map((item) => (
          <FilterItemCheckbox key={item}>{item}</FilterItemCheckbox>
        ))}
      </FilterListTemplate>
    </div>
  );
};

export default RealtyFilter;
