import React from 'react';
import styled from 'styled-components';
import GoodsCard from './GoodsCard';

const CarsCardBlock = styled(GoodsCard)`
  .car-name {
    margin-bottom: 0.4em;
    line-height: 1.5;
  }
`;

const CarsCard = ({ imgIndex }) => {
  return (
    <CarsCardBlock to="X4 M F98 3.0T xDrive  (480)">
      <GoodsCard.ImgBox>
        <img
          src={`${process.env.PUBLIC_URL}/images/cars_list_thumb_${imgIndex}.jpeg`}
          loading="lazy"
          alt=""
        />
      </GoodsCard.ImgBox>
      <div className="car-name">X4 M F98 3.0T xDrive  (480)</div>
      <GoodsCard.Price> 85/</GoodsCard.Price>
      <GoodsCard.Info info={['20', '6km']} />
      <GoodsCard.Location location="" uploadTime="20 " />
      <GoodsCard.Reaction chat={22} like={184} />
    </CarsCardBlock>
  );
};

export default CarsCard;
